body {
  font-family: 'Roboto';
}
header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  height: 100vh;
  width: 15.62vw;
  background-color: #242b2e;
  color: #6b7072;
  box-shadow: 9px 0px 13px 0px #848484;
}
.st0 {
  fill: #364048;
}
.st1 {
  fill: #364048;
}
.logo {
  width: 100%;
  height: 11.57vh;
  background-color: #fff;
}
.logo svg {
  width: 6vw;
}
.main_nav a {
  transition: 0.4s;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.93vmax;
  line-height: 1.66vmax;
  height: 3.22vmax;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-content: center;
  background-color: #2f373b;
  align-items: center;
  background: #333b3f; /* Old browsers */
  background: -moz-linear-gradient(top, #333b3f 0%, #2e363a 98%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #333b3f 0%, #2e363a 98%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #333b3f 0%, #2e363a 98%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding-right: 1.3vmax;
  width: 100%;
}
.main_nav a:hover {
  background: #31bdbb; /* Old browsers */
  background: -moz-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #31bdbb 0%, #18a3a1 79%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.filter__title {
  color: #1ab5b3;
  fill: #1ab5b3;
  font-size: 0.98vmax;
  padding: 1.25vmax;
  text-align: right;
}
.filter__title svg {
  margin-left: 0.62vmax;
  width: 0.9vmax;
  height: 0.9vmax;
}
.filter li {
  width: 100%;
  text-align: right;
}
.filter a {
  font-size: 0.67vmax;
  line-height: 1.25vmax;
  text-align: right;
  padding: 0.41vmax 1.25vmax;
  text-decoration: none;
  color: #616669;
}
.filter a:hover {
  color: #fff;
}
.filter a.active {
  color: #fff;
}
